import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { StrategiesComponent } from './strategy/strategy.list.component';
import { StrategyAddComponent } from './strategy/strategy.add.component';
import { StrategyBidComponent } from './strategy/strategy.bid.component';
import { BidLogsComponent } from './strategy/bid.logs.component';
import { PpdUsersComponent } from './user/ppduser.list.component';


const routes: Routes = [
  {path: 'strategies',component: StrategiesComponent},
  {path: 'bids',component: BidLogsComponent},
  {path: 'accounts',component: PpdUsersComponent},
  {path: 'add_strategy',component: StrategyAddComponent}
  ]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



