import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule }   from '@angular/router';

import { GlobalService } from './global.service';

// import { DynamicFormComponent }         from './dynamic-form.component';
// import { DynamicFormQuestionComponent } from './dynamic-form-question.component';

import {AppRoutingModule} from './app-routing.module';
import { CookieService } from 'angular2-cookie/services/cookies.service'

import { AppComponent } from './app.component';
import { StrategiesComponent } from './strategy/strategy.list.component';
import { StrategyAddComponent } from './strategy/strategy.add.component';
import { StrategyBidComponent } from './strategy/strategy.bid.component';
import { BidLogsComponent } from './strategy/bid.logs.component';

import { StrategyService } from './strategy/strategy.service';
import { BidLogsService } from './strategy/bid.logs.service';
import { LoggerService } from './logger.service';

import { PpdUsersComponent } from './user/ppduser.list.component';
import { UserService } from './user/user.service';
import { PpdUser } from './user/user';

import { AvailablePipe, PercentagePipe } from './common.pipe';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
//    InMemoryWebApiModule.forRoot(InMemoryDataService),
    AppRoutingModule
  ],
  declarations: [
    AppComponent,
    StrategiesComponent,
    BidLogsComponent,
    StrategyBidComponent,
    StrategyAddComponent,
    PpdUsersComponent,
    AvailablePipe,
    PercentagePipe
  ],
  providers: [ StrategyService, BidLogsService, UserService, LoggerService, CookieService, GlobalService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



