import { Injectable } from '@angular/core';
import { Headers, Http, Response, RequestOptions } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { PpdUser, User } from './user';
import { LoggerService }      from '../logger.service';
import { GlobalService }      from '../global.service';

@Injectable()
export class UserService {
  private UPDATE_PPD_USER_URL = 'api/ppduser/'   // PPD 
  private GET_RELATIVE_PPD_USERS_URL = 'http://localhost:8080/api/ppdusers/'   // PPD 
  private GET_USER_INFO_URL = 'api/user/'    // 

  constructor(private logger: LoggerService, private http: Http, private global: GlobalService) { }

  getPpdUsers(user: string): Promise<PpdUser[]> {
    this.logger.debug("[UserService]" + this.GET_RELATIVE_PPD_USERS_URL + this.global.current_user.ppdName);
    return this.http.get(this.GET_RELATIVE_PPD_USERS_URL + user)
                  .toPromise()
                  .then(response =>  response.json() as PpdUser[])
                  .catch(this.handleError);
  }

  getPpdUsersObservable(user: string): Observable<PpdUser[]> {
    this.logger.debug("[UserService Observable]" + this.GET_RELATIVE_PPD_USERS_URL  + this.global.current_user.ppdName);
    return this.http.get(this.GET_RELATIVE_PPD_USERS_URL +  user)
                  .map(response => this.extractData(response))
                  .catch(this.handleError);
  }

  updatePpdUserSetting(pdu: PpdUser) {
    this.logger.debug("[UserService.updatePpdUserSetting]Ppd");
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.UPDATE_PPD_USER_URL + pdu.ppdName, { name }, options)
               .toPromise()
               .then(response => null)
               .catch(this.handleError);
  }

  private extractData(res: Response): PpdUser[] {
    this.logger.debug("[UserService.extractData]." + res.json());
    return  res.json() as PpdUser[];
  }

  private handleError (error: Response | any) {
    // In a real world app, you might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    this.logger.error("[UserService]" + errMsg);
    return Observable.throw(errMsg);
  }
}



