import { Injectable } from '@angular/core';
import { Headers, Http, Response, RequestOptions } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { Strategy, Bid } from './strategy';
import { PpdUser} from '../user/user';
import { LoggerService} from '../logger.service';
import { GlobalService} from '../global.service';

import { BIDS } from './mock';

@Injectable()
export class StrategyService {
  private GET_STRATEGY_URL = 'http://localhost:8080/api/strategies/';
  private SUB_STRATEGY_URL = 'http://localhost:8080/api/strategy/sub';

  constructor(private logger: LoggerService, private http: Http, private global: GlobalService) { }

  getStrategiesObservable(user: string): Observable<Strategy[]> {
    return this.http.get(this.GET_STRATEGY_URL + user)
                    .map(response => this.extractStrategyData(response))
                    .catch(this.handleError);
  }

  private extractStrategyData(res: Response): Strategy[] {
    this.logger.debug("[StrategyService.extractData]." + res.json());
    return  res.json() as Strategy[];
  }

  private handleError (error: Response | any) {
    // In a real world app, you might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    this.logger.error("[UserService.handleError]" + errMsg);
    return Observable.throw(errMsg);
  }

  getBids(strategy: Strategy): Promise<Bid[]> {
    return Promise.resolve(BIDS);
  }

  subStrategy(strategy: Strategy) {
    this.logger.debug("" + "{sid:" + strategy.id+ ", ppdName:\"" + this.global.current_user.ppdName + "\",sub:" + strategy.status + "}");

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.put(this.SUB_STRATEGY_URL, "{\"sid\":" + strategy.id+ ", \"ppdName\":\"" + this.global.current_user.ppdName + "\",\"sub\":" + strategy.status + "}", { headers: headers })
                    .map(response => "ok")
                    .catch(this.handleError);
  }
}



