import { Component, OnInit } from '@angular/core';
import { Bid } from './strategy';
import { BidLogsService } from './bid.logs.service';

@Component({
  templateUrl: './bid.logs.html',
})
export class BidLogsComponent implements OnInit {
  title = "";
  bids: Bid[];

  constructor(private bidLogsService: BidLogsService) { }

  getBids(): void {
    this.bidLogsService.getBids().then(bids => this.bids = bids);
  }

  ngOnInit(): void {
    this.getBids();
  }
}



