import { Injectable } from '@angular/core';

@Injectable()
export class LoggerService {
  logs: string[] = [];

  info(msg: any)  { this.log(`INFO: ${msg}`); }
  debug(msg: any) { this.log(`DEBUG: ${msg}`); }
  error(msg: any) { this.log(`ERROR: ${msg}`, true); }

  private log(msg: any, isErr = false) {
    this.logs.push(msg);
    isErr ? console.error(msg) : console.log(msg);
  }
}



