import { Component, OnInit } from '@angular/core';
import { Strategy } from './strategy';
import { PpdUser } from '../user/user';

import { StrategyService } from './strategy.service';
import { GlobalService } from '../global.service';
import { LoggerService } from '../logger.service';
import { UserService } from '../user/user.service';

@Component({
  templateUrl: './strategy.list.html',
  styleUrls: ['./strategy.list.css']
})
export class StrategiesComponent implements OnInit {
  strategies1:Strategy[] = [];
  strategies2:Strategy[] = [];
  strategies3:Strategy[] = [];

  title:String;
  selectedStrategy:Strategy;

  ppdUser: PpdUser;
  constructor(private strategyService: StrategyService, private userService: UserService,
    private logger: LoggerService, private global: GlobalService) {}


  private handleStrategies(strategies: Strategy[]) {
    for (let s of strategies) {
      if(s.kind === 1) this.strategies1.push(s);
      else if(s.kind === 2) this.strategies2.push(s);
      else if(s.kind === 3) this.strategies3.push(s);
    }
  }

  onClick(strategy: Strategy): void {
    strategy.status = !strategy.status;
    // todo:
    this.logger.debug("" + strategy.status);
    this.strategyService.subStrategy(strategy).subscribe(
        data =>  this.logger.debug(data),
        err => this.logger.error(err));
  }

  updateCurrentUser(pdu: PpdUser): void {
    this.userService.updatePpdUserSetting(pdu);
  }

  onSelect(strategy: Strategy): void {
    this.selectedStrategy = strategy;
    this.title = strategy.name;
  }

  ngOnInit(): void {
    this.strategyService.getStrategiesObservable(this.global.current_user.ppdName).subscribe(
                  ss => this.handleStrategies(ss),
                  error => this.logger.error(error));
  }
}



