import { Component, OnInit } from '@angular/core';
import { UserService } from './user.service';
import { PpdUser, User } from './user';
import { GlobalService } from '../global.service';
import { LoggerService } from '../logger.service';

@Component({
  templateUrl: './ppduser.list.html',
  styleUrls: ['./user.css']
})
export class PpdUsersComponent implements OnInit {
  ppdUsers: PpdUser[];
  user: User;

  constructor(private userService: UserService, private global: GlobalService, private logger: LoggerService) { }

  getPpdUsers(): void {
    this.userService.getPpdUsersObservable(this.global.current_user.ppdName).subscribe(
                       users => this.ppdUsers = users,
                       error => this.logger.error(error));
  }

  ngOnInit(): void {
    this.getPpdUsers();
  }
}



