import { Component, OnInit } from '@angular/core';
import { Title }     from '@angular/platform-browser';

import { UserService } from './user/user.service';
import { PpdUser, CURRENT_PPD_USER } from './user/user';
import { LoggerService }      from './logger.service';
import { GlobalService } from './global.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  ppdUsers: PpdUser[];

  public constructor(private titleService: Title, private userService: UserService,
    private logger: LoggerService, private global: GlobalService ) {}

  ngOnInit(): void {
    this.titleService.setTitle("34");
    this.global.current_user = CURRENT_PPD_USER;
//    this.userService.getPpdUsers().then( users => this.ppdUsers = users));
    this.userService.getPpdUsersObservable(this.global.current_user.ppdName).subscribe(
                       users => this.ppdUsers = users,
                       error => this.logger.error(error));
  }

  changeCurrentUser(pdu: PpdUser): void {
    this.global.current_user = pdu;
  }
}



